import uniLoadMore from '../../../components/uni-load-more.vue';
export default {
	components: {//2注册组件
		uniLoadMore
	},
	data() {
		return {
			token:'',
			page:1,
			allPages:'',
			contentList:[],
			loadingText: '加载中...',
			loadingType: 0,//定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown:'上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		}
	},
	created:function(){
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	onReachBottom:function(){
		let _this=this;
		_this.userExchange()
	},
	mounted:function(){
		let _this=this;
		_this.userExchange()
	},
	methods: {
		//记录
		userExchange:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
				page:_this.page,
			};
			_this.loadingType = 1;
			uni.showNavigationBarLoading()
			_this.$axios(_this.$baseUrl.userExchange,data).then(res =>{
				if(res.data.status==1){
					if (res.data.result.list == null ||res.data.result.list==undefined ||res.data.result.list=='' ) {//没有数据
					    _this.loadingType = 2;
					    uni.hideNavigationBarLoading();//关闭加载动画
					    return;
					}
					_this.page++
					_this.contentList = _this.contentList.concat(res.data.result.list);
					_this.allPages = res.data.result.pages;
					_this.loadingType = 0;//将loadingType归0重置
					uni.hideNavigationBarLoading();//关闭加载动画
				}else{
					_this.loadingType = 2;
					uni.hideNavigationBarLoading();//关闭加载动画
				}
			}).catch(error =>{
				
			})
		},
	}
}
